import { useSelector, useDispatch } from "react-redux";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import { changeAuthorized, changeLogout } from "../features/getUser/getUserData";

import { Circles } from "react-loader-spinner"

import { NavLink } from "react-router-dom";
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import cross from "./static/images/cross.png";
import icon_eye from "./static/images/icons8-eye-96.png"

import { fetchPosts } from "../features/post/PostSlice";

import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
import { unwrapResult } from "@reduxjs/toolkit";
import PageNotFound505 from "./PageNotFound505";

const schema = yup.object().shape({
    email: yup.string().email().required(),
    password: yup.string().min(4).max(32).required(),
});

const SignIn = ({ authorized, setAuthorized }) => {
    const postStatus = useSelector((state) => state.post.status);
    const flag = useSelector((state) => state.post.fetchDataFlag);
    const [checkError, setCheckError] = useState(false);
    // const finalFlagSuccess = useSelector((state) => state.reset.finalFlagSuccess)

    const navigate = useNavigate();
    const dispatch = useDispatch()

    // ui state
    const [rememberHim, setRememberHim] = useState(false);
    const [typePassword, setTypePassword] = useState("password");

    const { register, handleSubmit, formState: { errors }, resetField, reset } = useForm({
        resolver: yupResolver(schema),
    });
    const onSubmitHandler = (data) => {
        console.log({ data });
        dispatch(fetchPosts({ url: "signin", gmail: data.email, password: data.password, rememberHim: rememberHim }))
            .then(unwrapResult)
            .catch((err) => {
                if (err.message === "Network Error") {
                    // navigate("/505")
                    setCheckError(true)
                }
            })
    }
    

    useEffect(() => {
        if (flag) {
            console.log("test");
            dispatch(changeLogout())
            dispatch(changeAuthorized(true))
            setAuthorized(true)
            navigate("/user-profile");
            reset()
        }
    }, [flag])

    if (checkError) {
        return (
             <>
                <PageNotFound505 />
             </>
        )
   }

    return (
        <>
            <ToastContainer />
            <form onSubmit={handleSubmit(onSubmitHandler)}>
            <div className="centered-text">
                <h1>     ?</h1>
            </div>
            <div className="container te">
                <div className="input-group mb-3 rol">
                    <input
                        type="email"
                        className="form-control ert"
                        placeholder=" "
                        aria-label="Recipient's username"
                        aria-describedby="button-addon2"
                        {...register("email")}
                    />
                    <button
                        type="button"
                        className="btn ss"
                        onClick={() => {
                            resetField("email");
                        }}
                    >
                        <img src={cross} />
                    </button>
                </div>
                <p>{errors.email?.message}</p>
                <p>{}</p>
                <div className="input-group mb-3 rol">
                    <input
                        type={typePassword}
                        className="form-control ert"
                        placeholder=""
                        aria-label="Recipient's username"
                        aria-describedby="button-addon2"
                        {...register("password")}
                    />
                    <button
                        type="button"
                        className="btn ss"
                        onClick={() => {
                            (typePassword == "password") ? setTypePassword("test") : setTypePassword("password")
                        }}
                    >
                        <img src={icon_eye} />
                    </button>
                </div>
                <p>{errors.password?.message}</p>
                <p className="objectt">' </p>
                <input
                    type="checkbox"
                    id="myCheckbox"
                    className="objectt"
                    checked={rememberHim}
                    onChange={() => {
                        (rememberHim) ? setRememberHim(false) : setRememberHim(true);

                    }}
                />
                <div className="silll">
                    <NavLink to="/reset-password" style={{ display: 'block' }} > ' </NavLink>
                    <NavLink to="/sign-up " style={{ display: 'block', marginTop: '2%'}}></NavLink>
                </div>
            </div>
            <div className="kn">
                <button
                    type="submit"
                    className="btn ss"
                    style={{ backgroundColor: "#40ABCF", color: "white", fontWeight: "bold" }}
                    disabled={(postStatus === "loading") ? true : false}
                >
                    {(postStatus === "loading") ? (
                        
                        <Circles
                            color="#00FFFF"
                            height={25}
                            width={60}
                            className="m-5"
                            
                        />
                    ) : (
                        <span

                        ></span>
                    )}
                </button>
            </div>
            </form>
        </>
    )
}

export default SignIn